.elementor-text-box {
  .icon-image {
    width: 40px;
    margin-right: 15px;
    @include flex(0 0 auto);
  }
  .title {
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    color: #707070;
    margin-bottom: 10px;
  }
  .description {
    font-size: 12px;
    color: #707070;
  }
}